.App {

  position: relative;

  width: 100%;
  height: 100%;

  overflow: hidden;
  background: #222;
  /* font-family: 'Bebas Neue', cursive; */
  font-family: 'Rubik', sans-serif;
}

.NoSpace{
  margin: 0;
}


.App .page {

  position: relative;

  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  font-size: 50px;
  /* text-transform: uppercase; */

}

.App .page h1{
  color: white;
  
}

.App .page h1 a{
  text-decoration: none;
  color: white;
}

.h1Custom{
  color: white;
  text-shadow: 0px 0px 11px rgb(31, 31, 31);
}

.App .page h2 span:first-of-type {
  text-shadow: 2px 2px 3px rgb(27, 27, 27);
  font-weight: 600;
  font-size: 50px;
  color: rgb(141, 141, 141);
}

.App .page h2 span:last-of-type {
  text-shadow: 2px 2px 3px rgb(31, 31, 31);
  font-size: 50px;
  font-weight: 600;
  color: rgba(255, 255, 255, 1);
}

.App .red {
  background-image: url("./assets/pexels-heorhii-heorhiichuk-994883.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  /* background: #DC143C; */
}

.App .orange {
  background-image: url("./assets/pexels-jessica-lewis-583846.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  /* background-position: center; */
}

.App .yellow {
  background-image: url("./assets/pexels-negative-space-160107.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.App .green {
  background-image: url("./assets/pexels-jessica-lewis-593322.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.App .blue {
  background-image: url("./assets/pexels-efdal-yildiz-917494.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.App .purple {
  background-image: url("./assets/pexels-stephan-seeber-1054218.jpg");
  background-repeat: no-repeat;
  background-size: cover;

}

.App .end{
  background-image: url("./assets/pexels-bri-schneiter-346529.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.App .controls,
.App .pagination {

  position: absolute;

  padding: 1vw;

  background-color: #fff;
  border-radius: 5px;
  box-shadow: 0 0 50px rgba(0, 0, 0, .25);

}

.App .controls {
  top: 1vw;
  right: 1vw;
}

.App .controls h3 {
  font-size: 16px;
  margin: 0 0 10px;
}

.App .pagination {
  bottom: 1vw;
  left: 1vw;
}

@media (max-width: 768px) {

  .App .page {
      font-size: 22px;
  }

  .App .controls {
      top: 2vw;
      right: 2vw;
      padding: 2vw;
  }

  .App .controls h3 {
      margin: 0 0 5px;
  }

}

